import React from 'react';
import 'styles/pages/AboutPage.scss';
import DSHS from 'assets/imgs/dshs.svg';
import SCHOOL from 'assets/imgs/school.svg';
import FINDER from 'assets/imgs/finder.svg';

import Skill1 from 'assets/imgs/skill1.svg';
import Skill2 from 'assets/imgs/skill2.svg';

function AboutPage() {
    return (
        <div className='AboutPage'>
            <section className='About1'>
                <div className="Stack">
                    <img className='SchoolImg' src={DSHS} alt="" style={{ height: '100%' }} />
                    <div className="Block">
                        <h4 className='w7'>대전대신고등학교</h4>
                        <p className='w4'>대전대신고는 학생의 자존감 향상을 통한 생명 살리기와 각자의 꿈과 끼를 살리는 행복 교육을 실현하는 학교이다. 이와 같이 학교 혁신을 위해 자율형 사립고로 전환하였으며, 창의성 향상과 인성교육을 위하여 다음과 같이 다섯 개 중점영역을 설정하여 운영되고 있다.</p>
                    </div>


                    <div className="Block Web popEffect" style={{ backgroundColor: 'rgba(41, 232, 155, 0.2)' }}>
                        <img src={SCHOOL} alt="" />
                        <h5 className='w7' onClick={() => { window.open('https://dshs.kr', '__blank') }}>학교 홈페이지<br />바로가기</h5>
                    </div>
                </div>
                <h1 className='w6'>저는 현재<br /><span className='green w7'>대전대신고등학교</span>에<br />재학중입니다.</h1>
            </section>


            <section className='About2'>
                <div className="Stack">
                    <div className="Block Finder">
                        <img src={FINDER} alt="FINDER" className='FinderLogo' />
                    </div>

                    <div className="Block">
                        <h4 className='w7'>FINDER</h4>
                        <p className='w4'>수학을 중심으로 한 STEAM 융합 동아리로, MAKER 활동 및 연구활동을 진행하고 있다. 최근에는 4차 산업혁명의 영향으로 인공지능 역량을 강화하기 위해 노력하는 중이다.</p>
                    </div>
                </div>
                <h1 className='w6'>교내 공학 동아리<br /><span className="blue w7">FINDER</span>에서<br />활동중이며,</h1>

            </section>


            <section className='About3'>
                <div className="Stack">
                    <div className="Skill">
                        <img src={Skill1} alt="" />
                        <span>
                            <h2>인공지능</h2>
                            <h6 className='w4'>Artificail Intelligence</h6>
                        </span>
                    </div>

                    <div className="Skill">
                        <img src={Skill2} alt="" />
                        <span>
                            <h2>웹/앱 개발</h2>
                            <h6 className='w4'>Web/App Development</h6>
                        </span>
                    </div>
                </div>

                <h1 className='w6'><span className="green w7">SW 프로그래밍</span>을<br />담당하고 있습니다.</h1>
            </section>
        </div>
    );
}

export default AboutPage;