import React, { useState } from 'react';
import 'styles/components/Director.scss';
import Arrow from 'assets/imgs/arrow.svg';
import { useNavigate } from 'react-router-dom';

function Director({ placeholder = '여기를 눌러 원하는 정보를 찾아보세요!', direction = 'bottom' }) {
    const [IsEnable, SetIsEnable] = useState(false);
    const navigate = useNavigate();
    function Goto(url) {
        navigate(url);
        SetIsEnable(false);
    }

    const ArrowRotate = {style:{transform:(direction === 'bottom' ? 'rotate(0deg)' : 'rotate(180deg)')}};

    const onClickHadle = () => {
        SetIsEnable(!IsEnable);
    };

    return (
        <>
            <div className='Director' onClick={onClickHadle}>
                <h5>{placeholder}</h5>
                <div className={'Arrow ' + (IsEnable ? 'ArrowEnable' : '')}>
                    <img src={Arrow} {...ArrowRotate} alt="" />
                </div>

            </div>
            <div className={"DirectorModal " + (IsEnable ? 'DirectorModalEnable' : '')}>
                <h2 className='popEffect' onClick={() => { Goto('/about') }}>나에 관하여</h2>
                <h2 className='popEffect' onClick={() => { Goto('/skill') }}>할 수 있는 것들</h2>
                <h2 className='popEffect' onClick={() => { Goto('/portfolio') }}>해왔던 것들</h2>
                <h2 className='popEffect' onClick={() => { Goto('/contact') }}>연락하기</h2>
            </div>
        </>

    );
}

export default Director;